import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dragZoomNotifier: true
  },
  mutations: {
    stopDragZoomNotifier:((state) => {
      state.dragZoomNotifier=false
    }),
  },
  actions: {
  },
  modules: {
  },
  getters: {
    getDragZoomNotifier: state => {
      return state.dragZoomNotifier;
    }
  }
})
